import React, { useEffect } from 'react'
import { format } from 'date-fns'

import '../components/queryTrace/queryTrace.scss'
import API from '../api/Api'

import Table from '../components/table/Table'
import DefaultTemplate from '../components/template/DefaultTemplate'


const PostingLookup = () => {
  const [data, setData] = React.useState([])

  const columns = [
    {
      field: 'issue_datetime',
      title: '발급일시',
      render: rowData => format(new Date(rowData.issue_datetime), 'yyyy-MM-dd HH:mm:dd')
    },
    {
      field: 'issue_number',
      title: '발급번호'
    },
    {
      field: 'from_name',
      title: '송신자-이름',
    },
    {
      field: 'from_license_num',
      title: '송신자-식별번호(사업자등록번호)'
    },
    {
      field: 'to_name',
      title: '수신자-이름'
    },
    {
      field: 'to_pi',
      title: '수신자-PI값'
    },
    {
      field: 'send_date',
      title: '송신날짜'
    },
    {
      field: 'recv_date',
      title: '수신날짜',
    },
    {
      field: 'read_date',
      title: '열람일자'
    },
    {
      field: 'doc_title',
      title: '문서제목'
    },
    {
      field: 'doc_hash',
      title: '본문정보값'
    },
    {
      field: 'issue_date',
      title: '증명서 발급일시(최초발급일자)'
    }
  ]

  const init = async () => {
    const res = await API.getCertHistory()

    setData(res)
  }

  useEffect(() => {
    init()
  }, [])


  return (
    <DefaultTemplate>
      <Table
        scrollable
        term
        termColumn="issue_datetime"
        title="수발신 증명서 발급 이력"
        columns={columns}
        data={data}
        options={{
          filtering: true,
          exportButton: true
        }}
      />
    </DefaultTemplate>
  )
}

export default PostingLookup